import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withStyles, Typography, Grid, Table, TextField, Paper, TableCell, TableHead, TableRow, TableContainer, IconButton, TableBody, Card, Checkbox } from '@material-ui/core';
import { AiFillCaretLeft, AiFillCaretRight } from "react-icons/ai";
import ReactApexChart from "react-apexcharts";
import { FiChevronDown, FiChevronUp } from "react-icons/fi";
import { BiDotsVerticalRounded } from "react-icons/bi";
import { MdOpenInNew } from "react-icons/md";
import { getAllCustomers, getCustomersByDropdown, getCustomerStatusReports, getCustomersViewCount } from '../../../store/actions/customer_action';
import exportblacksvg from '../../../assets/images/svg/exportblack.svg';
import filterblack from '../../../assets/images/svg/filterblack.svg';
import refresh from '../../../assets/images/svg/refresh.svg';


/**Created common styles for UI.*/
const styles = theme => ({
	root: {
		padding: theme.spacing(4)
	},
	buttonVariant: {
		boxShadow: "0px 0px 0px 0px #E1E7ED",
		backgroundColor: "#57B78C",
		margin: "5px 0px",
		color: "#fff",
		borderRadius: 6,
		fontSize: 14,
		padding: "5px 20px",
		height: "fit-content",
		textTransform: "none",
		fontFamily: "SourceSansPro-Regular",
		'&:hover': {
			backgroundColor: "#57B78C",
		},
	},
	card: {
		border: "1px solid #cccccc",
		boxShadow: "0px 0px 0px 0px rgba(0,0,0,0)",
		borderRadius: 0,
		backgroundColor: "#fff"
	},
	tableHead: {
		backgroundColor: "#fff",
	},
	tableRow: {
		padding: 0,
		height: '35px'
	},
	tableCell: {
		fontSize: 14,
		padding: "3px 10px",
		whiteSpace: 'initial',
		fontFamily: "SourceSansPro-Regular",
	},
	tableHeadCell: {
		padding: "10px 10px",
		fontSize: 15,
		fontWeight: "600",
		color: "#7B8395",
		backgroundColor: "#F0F2F480",
		borderRight: "1px solid #cccccc",
		fontFamily: "SourceSansPro-Regular",
	},
	tableHeadLastCell: {
		padding: "10px 10px",
		fontSize: 15,
		fontWeight: "600",
		color: "#000",
		backgroundColor: theme.palette.primary.light,
		fontFamily: "SourceSansPro-Regular",
	},
	inputRoot: {
		backgroundColor: "#FFFFFF",
		// border: "1px solid #E1E7ED",
		borderRadius: "4px",
		fontSize: "13x !important",
		"&:hover": {
			// border: "1px solid #57B78C",
			backgroundColor: "#FFFFFF"
		}
	},
	inputStyle: {
		fontFamily: "SourceSansPro-Regular",
		fontSize: "13x !important",
		padding: "5px !important",
		paddingRight: "32px !important",
		color: "#6E8CA8",
		opacity: 1,
		"&&:after": {
			color: "#57B78C",
		}
	},
	inputDrawerRoot: {
		backgroundColor: "#FFFFFF",
		border: "1px solid #E1E7ED",
		borderRadius: "4px",
		fontSize: "13x !important",
		fontFamily: "SourceSansPro-Regular",
		"&:hover": {
			border: "1px solid #57B78C",
			backgroundColor: "#FFFFFF"
		}
	},
	inputDrawerStyle: {
		fontFamily: "SourceSansPro-Regular",
		fontSize: "13x !important",
		padding: "10px !important",
		paddingRight: "32px !important",
		color: "#6E8CA8",
		opacity: 1,
		"&&:after": {
			color: "#57B78C",
		}
	},
	underline: {
		"&&&:before": {
			borderBottom: "none"
		},
		"&&:after": {
			borderBottom: "none"
		}
	},
	tablePageRoot: {
		display: "flex",
		justifyContent: 'space-between',
		backgroundColor: "#ffffff",
		border: "1px solid #cccccc",
		marginTop: 10
	},
	tablePageRow: {
		display: "flex",
		justifyContent: 'space-between',
	},
	tablePagediv: {
		display: "flex",
		justifyContent: 'space-between',
		padding: "5px 15px",
		borderRight: "1px solid #cccccc",
		alignItems: "center"
	},
	tablePagedivLast: {
		display: "flex",
		justifyContent: 'space-between',
		padding: "5px 15px",
		borderRight: "1px solid #cccccc",
		alignItems: "center"
	},
	tablePagedivBorder: {
		display: "flex",
		justifyContent: 'space-between',
		padding: "5px 15px",
		borderRight: "1px solid #cccccc",
		borderLeft: "1px solid #cccccc",
		alignItems: "center"
	},
	iconButton: {
		padding: 3,
		borderRadius: 0
	},
	textRowPage: {
		fontSize: 14,
		color: "#000",
		paddingRight: 10
	},
	paper: {
		minWidth: 540,
		borderRadius: 0,
		backgroundColor: "#E5E5E5",
		boxShadow: "0px 0px 0px 0px rgba(0,0,0,0)",
	},
	title: {
		textDecoration: 'none',
		fontSize: '15px',
		fontWeight: "600",
		color: theme.palette.common.black,
		fontFamily: "SourceSansPro-Regular",
	},
	cardA: {
		padding: "15px 10px",
		borderRadius: 5,
		boxShadow: "0px 0px 0px 0px #E1E7ED",
		textAlign: "center"
	},
	selectCardA: {
		padding: "15px 20px",
		borderRadius: 5,
		border: "2px solid #57B78C",
		boxShadow: "0px 0px 0px 0px #57B78C",
		textAlign: "center"
	},
	cardTextTitle: {
		color: "#666666",
		fontSize: 15,
		fontFamily: "SourceSansPro-Regular",
		textTransform: "uppercase"
	},
	cardTextSecondTitle: {
		color: "#000",
		fontSize: 25,
		padding: "5px 0px",
		fontWeight: "600",
		fontFamily: "SourceSansPro-Regular",
	},
	selectedCardTextTitle: {
		color: "#57B78C",
		fontSize: 15,
		fontFamily: "SourceSansPro-Regular",
		textTransform: "uppercase"
	},
	selectedCardTextSecondTitle: {
		color: "#57B78C",
		fontSize: 25,
		padding: "5px 0px",
		fontWeight: "600",
		fontFamily: "SourceSansPro-Regular",
	},
	plantitle: {
		padding: "8px 7px",
		color: "#000000",
		margin: "0px 3px",
		fontSize: 15,
		fontWeight: "600",
		cursor: "pointer",
		fontFamily: "SourceSansPro-Regular"
	},
	selectedPlanTitle: {
		padding: "8px 7px",
		color: "#57B78C",
		margin: "0px 3px",
		fontSize: 15,
		fontWeight: "600",
		cursor: "pointer",
		borderBottom: "2px solid #57B78C",
		fontFamily: "SourceSansPro-Regular"
	}
});


/**
 * @class
 * Class representing Customers component
 */
class Customers extends Component {
	constructor(props) {
		super(props);
		this.state = {
			page: 0,
			rowsPerPage: 5,
			select_customer_plan: "ALL",
			selected_customers: [],
			val:0,
			select_all:false
		};
	}

	componentDidMount = () => {
		this.props.dispatch(getAllCustomers())
		this.props.dispatch(getCustomersViewCount())
		this.props.dispatch(getCustomerStatusReports())
	}

	handleChangePage = (dir, length) => {
		if (dir === "LEFT") {
			if (this.state.page > 0) {
				this.setState({ page: this.state.page - 1 });
			}
		} else {
			if (length > (this.state.page + 1) * this.state.rowsPerPage) {
				this.setState({ page: this.state.page + 1 });
			}
		}
	};

	activePage = (dir, length) => {
		if (dir === "LEFT") {
			if (this.state.page === 0) {
				return false;
			}
		} else {
			if (length <= (this.state.page + 1) * this.state.rowsPerPage) {
				return false;
			}
		}
		return true;
	};

	handleChangeRowsPerPage = event => {
		this.setState({ rowsPerPage: event.target.value });
	};

	returnTotalPages = (length) => {
		if (length >= this.state.rowsPerPage) {
			return Math.ceil(length / this.state.rowsPerPage)
		} else {
			return 1
		}
	}

	selectAll = (e) => {
		const { allCustomers } = this.props;

		if (e.target.checked === true) {
			this.setState({ selected_customers: this.state.select_all === false ? allCustomers : [], select_all: !this.state.select_all })
		}
		if (e.target.checked === false) {
			this.setState({ selected_customers: this.state.select_all === false ? allCustomers : [], select_all: !this.state.select_all })
		}
	}

	selectCustomers = (e, item) => {

		if (e.target.checked === true) {

			this.setState({
				selected_customers: this.state.selected_customers.concat(item)
			})

		}
		if (e.target.checked === false) {

			this.setState({
				selected_customers: this.state.selected_customers.filter(i => i !== item)
			})

		}

	}
	returnColor = (status) => {
		let color = "#000000"
		switch (status) {
			case "Withdrwan":
				color = "#1F232E"
				break;
			case "Overdue":
				color = "#F3852A"
				break;
			case "Active":
				color = "#57B78C"
				break;
			case "Cancelled":
				color = "##E55562"
				break;
			default:
				break;
		}
		return color;
	}

	returnPiechart = (customerStatusReportsPie) => {
		if (customerStatusReportsPie.length > 0) {
			let series = JSON.parse(customerStatusReportsPie[0].series);
			let options = {
				chart: {
					width: 380,
					type: 'pie',
					stacked: false,
					fontFamily: "SourceSansPro-Regular",
					toolbar: {
						show: false,
					}
				},
				title: {
					text: customerStatusReportsPie[0].title,
					align: 'left',
					floating: true,
				},
				colors: ['#57B78C', '#FFC633', '#5EC8E6', '#B9B8B8'],
				labels: JSON.parse(customerStatusReportsPie[0].label),
				legend: {
					show: true,
					position: 'right',
					horizontalAlign: 'center',
				},
				responsive: [{
					breakpoint: 480,
					options: {
						chart: {
							width: 200
						},
						legend: {
							position: 'bottom'
						}
					}
				}]
			};
			return (
				<div>
					<ReactApexChart options={options} series={series} type="pie" height={300} />
				</div>
			)
		}
	}

	returnBarchart = () => {
		let series = [{
			name: 'This Week',
			type: 'area',
			data: [4400, 5500, 3100, 4700, 3100, 2600, 4000]
		}, {
			name: 'Last Week',
			type: 'line',
			data: [3300, 6900, 3000, 6100, 3100, 3500, 2900]
		}];
		let options = {
			chart: {
				height: 250,
				type: 'line',
				stacked: false,
				fontFamily: "SourceSansPro-Regular",
				toolbar: {
					show: false,
				},
				zoom: {
					enabled: false,
				}
			},
			title: {
				text: "Total Customers",
				align: 'left',
				floating: true,
			},
			stroke: {
				curve: 'smooth',
				width: [2, 1],
				dashArray: [0, 8]
			},
			fill: {
				colors: ['#FEEFE8', '#FEEFE8'],
				opacity: 0.9,
				type: 'solid',
				gradient: {
					shade: 'dark',
					type: "horizontal",
					shadeIntensity: 0.5,
					gradientToColors: undefined,
					inverseColors: true,
					opacityFrom: 1,
					opacityTo: 1,
					stops: [0, 50, 100],
					colorStops: []
				},
			},
			colors: ["#E4B5A2", "#cccccc"],
			labels: ["23/6", "24/6", "25/6", "27/6", "28/6", "29/6", "30/6"],
			markers: {
				size: 0
			},
			legend: {
				show: true,
				position: 'top',
				horizontalAlign: 'right',
				inverseOrder: true,
				markers: {
					width: 30,
					height: 1,
				},
			},
			yaxis: {
				labels: {
					show: true,
					formatter: (value) => { return parseFloat(parseInt(value) / 1000).toString() + "k" },
				}
			},
			tooltip: {
				shared: true,
				intersect: false,
				y: {
					formatter: function (y) {
						if (typeof y !== "undefined") {
							return y.toFixed(0) + " points";
						}
						return y;
					}
				}
			}
		};
		return (
			<div>
				<ReactApexChart options={options} series={series} type="line" height={250} />
			</div>
		)
	}

	selectCustomerPlan = (value) => {
		if (value === "ALL") {
			this.setState({ select_customer_plan: value })
			this.props.dispatch(getAllCustomers());
		} else {
			let query = `CustomerPlan=${value}`
			this.setState({ select_customer_plan: value })
			this.props.dispatch(getCustomersByDropdown(query))
		}
	}

	/** rendering Customers information. */
	render() {
		const { classes, allCustomers, coutomersOverviewCount, customerStatusReportsPie } = this.props;
		const { rowsPerPage, page } = this.state;
		return (
			<div className={classes.root}>
				<div style={{ display: "flex", justifyContent: "space-between" }}>
					<Typography className={classes.title}>
						Overview
					</Typography>
					<div style={{ display: "flex" }}>
						<TextField
							style={{ margin: 5 }}
							variant="filled"
							value={this.state.val}
							InputProps={{ classes: { input: classes.inputDrawerStyle, underline: classes.underline, root: classes.inputDrawerRoot } }}
							select
							onChange={e => this.setState({ val: e.target.value })}
							SelectProps={{
								native: true,
							}}
						>
							<option value="7" style={{ fontFamily: "SourceSansPro-Regular" }}>
								Last 7 days Orders (23 Jun - 30 Jun)
							</option>
							<option value="15" style={{ fontFamily: "SourceSansPro-Regular" }}>
								Last 15 days Orders (23 Jun - 30 Jun)
							</option>
							<option value="25" style={{ fontFamily: "SourceSansPro-Regular" }}>
								Last 25 days Orders (23 Jun - 30 Jun)
							</option>
						</TextField>
					</div>
				</div>
				<Grid container spacing={4}>
					<Grid item xs={12} sm={6} md={3}>
						{coutomersOverviewCount.length > 0 && <Card className={classes.cardA}>
							<Typography className={classes.cardTextTitle}>Subscribers</Typography>
							<Typography className={classes.cardTextSecondTitle}>{coutomersOverviewCount[0].subscribersCount}</Typography>
						</Card>}
					</Grid>
					<Grid item xs={12} sm={6} md={3}>
						{coutomersOverviewCount.length > 0 && <Card className={classes.selectCardA}>
							<Typography className={classes.selectedCardTextTitle}>Subscription Overdue</Typography>
							<Typography className={classes.selectedCardTextSecondTitle}>{coutomersOverviewCount[0].subscriptionOverDueCount}</Typography>
						</Card>}
					</Grid>
					<Grid item xs={12} sm={6} md={3}>
						{coutomersOverviewCount.length > 0 && <Card className={classes.cardA}>
							<Typography className={classes.cardTextTitle}>Leads </Typography>
							<Typography className={classes.cardTextSecondTitle}>{coutomersOverviewCount[0].leadsCount}</Typography>
						</Card>}
					</Grid>
					<Grid item xs={12} sm={6} md={3}>
						{coutomersOverviewCount.length > 0 && <Card className={classes.cardA}>
							<Typography className={classes.cardTextTitle}>Subscription Widthdrawn</Typography>
							<Typography className={classes.cardTextSecondTitle}>{coutomersOverviewCount[0].subscriptionWithdrawnCount}</Typography>
						</Card>}
					</Grid>
					<Grid item xs={12} md={6}>
						<Card className={classes.card} style={{ boxShadow: "none", height: "100%" }} >
							{this.returnBarchart()}
						</Card>
					</Grid>
					<Grid item xs={12} md={6}>
						<Card className={classes.card} style={{ boxShadow: "none", height: "100%" }}>
							{this.returnPiechart(customerStatusReportsPie)}
						</Card>
					</Grid>
					<Grid item xs={12}>
						<TableContainer component={Paper} className={classes.paper}>
							<div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", backgroundColor: "#fff", padding: 5 }}>
								<div style={{ display: "flex" }}>
									<Typography onClick={() => this.selectCustomerPlan("ALL")} className={this.state.select_customer_plan === "ALL" ? classes.selectedPlanTitle : classes.plantitle}>All Customers</Typography>
									<Typography onClick={() => this.selectCustomerPlan("Subscriber")} className={this.state.select_customer_plan === "Subscriber" ? classes.selectedPlanTitle : classes.plantitle}>Subscribers</Typography>
									<Typography onClick={() => this.selectCustomerPlan("One-Time")} className={this.state.select_customer_plan === "One-Time" ? classes.selectedPlanTitle : classes.plantitle} >Leads</Typography>
								</div>
								<div style={{ display: "flex", alignItems: "center" }}>
									<div style={{ display: "flex", alignItems: "center" }}>
									<img src={exportblacksvg} alt="exportblacksvg" size={35} /><Typography style={{ alignItems: "center", fontSize: 15, margin: "0px 5px", fontFamily: "SourceSansPro-Regular" }}> Export</Typography></div>
									<div style={{ display: "flex", alignItems: "center" }}>
									<img src={filterblack} alt="filterblack" size={15} /><Typography style={{ alignItems: "center", fontSize: 15, margin: "0px 5px", fontFamily: "SourceSansPro-Regular" }}>  Filter</Typography></div>
									<div style={{ display: "flex", alignItems: "center" }}>
									<img src={refresh} alt="refresh" size={15} /><Typography style={{ alignItems: "center", fontSize: 15, margin: "0px 5px", fontFamily: "SourceSansPro-Regular" }}> Refresh</Typography></div>
									<div style={{ borderLeft: "1px solid #666666", height: 30 }} />
									<div style={{ display: "flex", alignItems: "center" }}><IconButton><MdOpenInNew size={20} style={{ padding: "5px 10px" }} /></IconButton></div>
								</div>
							</div>
							<Table className={classes.card}>
								<TableHead className={classes.tableHead}>
									<TableRow >
										<TableCell align="left" className={classes.tableHeadCell}> 
										<Checkbox
											
											color="primary"
											onClick={(e) => this.selectAll(e)}
											checked={allCustomers.length === this.state.selected_customers.length}
											inputProps={{ 'aria-label': 'primary checkbox' }}
										/></TableCell>
										<TableCell align="left" className={classes.tableHeadCell}>
											<div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
												<Typography style={{
													fontSize: 15,
													fontWeight: "600",
													color: "#7B8395",
													textTransform: "uppercase",
													fontFamily: "SourceSansPro-Regular"
												}}>Customer Name </Typography>
											</div>
										</TableCell>
										<TableCell align="left" className={classes.tableHeadCell}>
											<div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
												<Typography style={{
													fontSize: 15,
													fontWeight: "600",
													color: "#7B8395",
													textTransform: "uppercase",
													fontFamily: "SourceSansPro-Regular"
												}}>Email </Typography>
											</div>
										</TableCell>
										<TableCell align="left" className={classes.tableHeadCell}>
											<div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
												<Typography style={{
													fontSize: 15,
													fontWeight: "600",
													color: "#7B8395",
													textTransform: "uppercase",
													fontFamily: "SourceSansPro-Regular"
												}}>Phone </Typography>
											</div>
										</TableCell>
										<TableCell align="left" className={classes.tableHeadCell}>
											<div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
												<Typography style={{
													fontSize: 15,
													fontWeight: "600",
													color: "#7B8395",
													textTransform: "uppercase",
													fontFamily: "SourceSansPro-Regular"
												}}>Plan </Typography>
											</div>
										</TableCell>
										<TableCell align="left" className={classes.tableHeadCell}>
											<div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
												<Typography style={{
													fontSize: 15,
													fontWeight: "600",
													color: "#7B8395",
													textTransform: "uppercase",
													fontFamily: "SourceSansPro-Regular"
												}}>Active Since </Typography>
												<div style={{ display: "grid", justifyContent: "space-between" }}>
													<FiChevronUp size={14} />
													<FiChevronDown size={14} />
												</div>
											</div>
										</TableCell>
										<TableCell align="left" className={classes.tableHeadCell}>
											<div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
												<Typography style={{
													fontSize: 15,
													fontWeight: "600",
													color: "#7B8395",
													textTransform: "uppercase",
													fontFamily: "SourceSansPro-Regular"
												}}>Last Service </Typography>
												<div style={{ display: "grid", justifyContent: "space-between" }}>
													<FiChevronUp size={14} />
													<FiChevronDown size={14} />
												</div>
											</div>
										</TableCell>
										<TableCell align="left" className={classes.tableHeadCell}>
											<div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
												<Typography style={{
													fontSize: 15,
													fontWeight: "600",
													color: "#7B8395",
													textTransform: "uppercase",
													fontFamily: "SourceSansPro-Regular"
												}}>Service Location </Typography>
											</div>
										</TableCell>
										<TableCell align="left" className={classes.tableHeadCell}>
											<div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
												<Typography style={{
													fontSize: 15,
													fontWeight: "600",
													fontFamily: "SourceSansPro-Regular",
													color: "#7B8395",
													textTransform: "uppercase"
												}}>Status </Typography>
											</div>
										</TableCell>
										<TableCell align="left" className={classes.tableHeadCell}></TableCell>
									</TableRow>
								</TableHead>
								<TableBody>
									{allCustomers
										.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
										.map((item, index) =>
											<TableRow
												key={index.toString()}
												className={classes.tableRow}
												hover>
												<TableCell className={classes.tableCell}>
													<Checkbox
														color="primary"
														checked={this.state.selected_customers.includes(item)||this.state.selected_customers.length === allCustomers.length}
														onClick={(e) => this.selectCustomers(e, item)}
														inputProps={{ 'aria-label': 'primary checkbox' }}
													/>
												</TableCell>
												<TableCell className={classes.tableCell}>
													{item.customerName}
												</TableCell>
												<TableCell className={classes.tableCell}>
													<Typography color="primary" style={{ textDecoration: "underline" }}>{item.email}</Typography>
												</TableCell>
												<TableCell className={classes.tableCell}>
													{item.phone}
												</TableCell>
												<TableCell className={classes.tableCell}>
													{item.plan}
												</TableCell>
												<TableCell className={classes.tableCell}>
													{item.activeSince}
												</TableCell>
												<TableCell className={classes.tableCell}>
													{item.lastService}
												</TableCell>
												<TableCell className={classes.tableCell}>
													{item.serviceLocation}
												</TableCell>
												<TableCell className={classes.tableCell} style={{ color: this.returnColor(item.status), fontWeight: "600" }}>
													{item.status}
												</TableCell>
												<TableCell className={classes.tableCell}>
													<IconButton aria-controls="simple-menu" aria-haspopup="true"><BiDotsVerticalRounded size={20} /></IconButton>
												</TableCell>
											</TableRow>)}
								</TableBody>
							</Table>
							<div className={classes.tablePageRoot} >
								<div className={classes.tablePageRow} >
									<div className={classes.tablePagediv}>
										<Typography className={classes.textRowPage}>
											Items per page :
										</Typography>
										<TextField
											variant="filled"
											value={this.state.rowsPerPage}
											onChange={(event) => this.handleChangeRowsPerPage(event)}
											InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
											select
											SelectProps={{
												native: true,
											}}>
											<option value={5}>
												5
											</option>
											<option value={15}>
												15
											</option>
											<option value={25}>
												25
											</option>
										</TextField>
									</div>
									<div className={classes.tablePagediv}>
										<Typography>
											{page * rowsPerPage + 1} - {rowsPerPage > allCustomers.length ? allCustomers.length : (page + 1) * rowsPerPage} of {allCustomers.length} items
										</Typography>
									</div>
								</div>
								<div className={classes.tablePageRow}>
									<div className={classes.tablePagedivBorder}>
										<Typography>
											{page + 1}  of {this.returnTotalPages(allCustomers.length)} pages
										</Typography>
									</div>
									<div className={classes.tablePagediv}>
										<IconButton className={classes.iconButton} disabled={!this.activePage("LEFT", allCustomers.length)} onClick={() => this.handleChangePage("LEFT", allCustomers.length)}>
											<AiFillCaretLeft size={20} color={this.activePage("LEFT", allCustomers.length) ? "#666666" : "#cccccc"} />
										</IconButton>
									</div>
									<div className={classes.tablePagedivLast}>
										<IconButton className={classes.iconButton} disabled={!this.activePage("RIGHT", allCustomers.length)} onClick={() => this.handleChangePage("RIGHT", allCustomers.length)}>
											<AiFillCaretRight size={20} color={this.activePage("RIGHT", allCustomers.length) ? "#666666" : "#cccccc"} />
										</IconButton>
									</div>
								</div>
							</div>
						</TableContainer>
					</Grid>
				</Grid>
			</div>
		);
	}
}

/** Get data from store and assign to props. */
const mapStateToProps = (state) => {
	return {
		allCustomers: state.customer_reducers.allCustomers,
		coutomersOverviewCount: state.customer_reducers.coutomersOverviewCount,
		customerStatusReportsPie: state.customer_reducers.customerStatusReportsPie
	}
};

export default connect(
	mapStateToProps
)(withStyles(styles)(Customers));
